// shortenUrl.js
const axios = require('axios');

const apiKey = 'pk_Yi3cYbEQK4Eo7KcA'; 

async function shortenUrlWithExpiration(longUrl, expirationDate) {
  const response = await axios.post('https://api.short.io/links', {
    originalURL: longUrl,
    expiration: expirationDate,
    domain: 'https://qendresaskenderitest.000webhostapp.com',
  }, {
    headers: {
      'accept': 'application/json',
      'authorization': `Bearer ${apiKey}`,
    }
  });

  return response.data.shortURL;
}

module.exports = shortenUrlWithExpiration;
